import { Button } from '@material-ui/core';
import React, { useState } from 'react';
import './TweetBox.css';
import { Avatar, button } from "@material-ui/core";
import db from './firebase';


function TweetBox() {
    const [tweetMessage,setTweetMessage] = useState('');
    const [tweetImage,setTweetImage] = useState('');
    const sendTweet = e => {
        e.preventDefault();
        db.collection('posts').add({
            displayName: 'Ronny',
            username: 'Rohiiit',
            verified: true,
            text: tweetMessage,
            image: tweetImage,
            avatar:'data:image/jpeg;base64,'
        });

        setTweetMessage('');
        setTweetImage('');
    }

    return (
        <div className='tweetBox'>
            <form>
                <div className='tweetBox__input'>
                    <Avatar src="data:image/jpeg;base64,"></Avatar>
                    <input onChange={(e) => setTweetMessage(e.target.value)} value={tweetMessage} placeholder="what is new?" type="text"></input>                     
                </div>
                <input value={tweetImage} onChange={(e) => setTweetImage(e.target.value)} className = "tweetBox__imageInput" placeholder="Enter Image Url" type="text"></input>
                <Button onClick={sendTweet} type='submit' className="tweetBox__tweetButton">Talk</Button>
            </form>
            
        </div>
    )
}

export default TweetBox
